body {
  color: #fff;
  background: linear-gradient(270deg, #bdc3c7, #2c3e50);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.title {
  font-family: Bodoni Moda, serif;
  font-size: 36px;
}

button {
  color: #000;
  cursor: pointer;
  background-color: #e8eef2;
  border: none;
  border-radius: 3px;
  margin: 5px;
  padding: 5px 20px;
  font-family: IBM Plex Serif, serif;
  font-size: 16px;
  transition: background-color .3s;
}

button:hover {
  background-color: #fff;
}

.counter-block, .color-block, .text-block, .number-block, .style-block, .slider, .size-block {
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 100px;
  margin-bottom: 20px;
  display: flex;
}

.counter-block {
  font-size: 20px;
}

.color-block {
  text-align: center;
  background-color: #fff;
  width: 250px;
  height: 200px;
  padding: 10px 15px;
}

.text-block, .number-block {
  font-size: 20px;
}

.style-block {
  color: #000;
  text-align: center;
  background-color: #fff;
  width: 200px;
  height: 150px;
  padding: 10px 15px;
  font-family: IBM Plex Serif, serif;
  font-size: 10px;
}

.slider {
  width: 350px;
  height: 250px;
  position: relative;
  overflow: hidden;
}

.slide {
  object-fit: cover;
  opacity: 0;
  width: 100%;
  height: 100%;
  transition: opacity .5s;
  position: absolute;
}

.slide.active {
  opacity: 1;
}

.size-block {
  color: #000;
  text-align: center;
  background-color: #deeefc;
  width: 250px;
  height: 200px;
  padding: 10px 15px;
}
